
import Vue from 'vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import * as base from '@/api/base'
import pingpp from 'pingpp-js'
import { checkLoginToLogin } from '@/api/login'

Vue.use(ToastPlugin)

export default {
name: 'in-stock',
components: {
XHeader,
Masker,
InlineXNumber,
XButton
},
data () {
return {
count: 1
}
},
methods: {
submitAction () {
let params = {
'num': this.count.toString(),
'channel': 'wx_wap'
}
base.fetch('/api/v1/purchase', params, response => {
let data = response.data
console.log(data)
let msg = data.msg || data.error || '请求失败'
if (data.code !== 0) {
this.$vux.toast.text(msg, 'middle')
return false
}
pingpp.createPayment(data.data, (result, err) => {
console.log(result)
localStorage.setItem('test', JSON.stringify(result))
console.log(err)
})
}, error => {
console.log(error)
})
}
},
created () {
checkLoginToLogin(this)
}
}



